.stats {
    grid-column: 2 / 3;
    grid-row: 2 /3;
}

/*----- start navigation --------*/
.stats__nav {
    width: 100%;
    background: var(--color-grey-light);
    margin-bottom: 3rem;
}
.stats__nav__list {
    list-style: none;
    display: flex;
}
.stats__nav__list__item {
    width: 15rem;
    background: var(--color-grey-dark);
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    border: 1px solid var(--color-grey);
    margin: 0 -1px;
    color: var(--color-white);
    cursor: pointer;
}
.stats__nav__list__item.active {
    background: var(--color-white);
    color: var(--color-primary);
    border-bottom: none;
}
/*------ end navigation ---------*/



/*-- start section and title----- */
.stats__section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5rem;
    position: relative;
}
.stats__section__title {
    position: relative;
}
.stats__section__title__cta {
    position: absolute;
    display: flex;
    top: 0;
    right: 0;    
}
/*-- end section and title----- */


/*------- start brand list -------- */
.stats__section__list {
    list-style: none;
    width: 55%;
    margin-right: 6rem;
}
.stats__section__list__item {
    display: flex;
    justify-content: center;
    flex-direction: column;
    cursor: pointer;
    margin-bottom: 2rem;
    border-radius: 5px;
}
.stats__section__list__item__brandContainer {
    display: flex;
    align-items: center;
    height: 4rem;
    background: var(--color-grey-light);
    padding: 0 1rem;
    margin-bottom: 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.stats__section__list__item__brand{
    margin-right: 2rem;
    width: 8rem;

}
.stats__section__list__item__viewBar {
    flex: 1;
    height: 5px;
    margin-right: 2rem;
    border-radius: 3px;
    position: relative;
} 
.stats__section__list__item__viewBar__inner {
    position: absolute;
    left: 0;
    height: 100%;
    background: var(--color-primary);
    border-radius: 3px;
}
.stats__section__list__item__modelsList {
    padding-top: 2rem;
    list-style: none;
    padding: 0 1rem;
    display: none;
    border-left: 1px solid var(--color-grey-light);
    border-right: 1px solid var(--color-grey-light);
    border-bottom: 1px solid var(--color-grey-light);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}
.stats__section__list__item.showList .stats__section__list__item__modelsList {
   display: block;
}
.stats__section__list__item__modelsList__item {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
}
.stats__section__list__item__modelsList__item:first-child{
    margin-top: 2rem;
}
.stats__section__list__item__modelsList__item__model {
    width: 8rem;
    margin-right: 2rem;
    text-align: end;
}
.stats__section__list__item__modelsList__item__viewBar {
    width: 15rem;
    height: 5px;
    margin-right: 2rem;
    position: relative;
    flex: 1;
}
.stats__section__list__item__modelsList__item__viewBar__inner {
    position: absolute;
    left: 0;
    height: 100%;
    background: rgb(102, 83, 83);
    border-radius: 3px;
} 
.stats__section__list__item__modelsList__item__viewCounter {

}
/*------- end brand list -------- */


/*--- start products list ----*/
.stats__products__list {
    width: 93%;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, 29rem);
    grid-template-rows: repeat(auto-fit, 25rem);
    grid-auto-rows: 25rem;  
    column-gap: 1rem;
    row-gap: 1rem;
    padding: 2rem 0;   
    justify-content: center;
    align-items: center;
    margin: 1rem 0;  
}

/*--- end products list ----*/

